@import url(https://fonts.googleapis.com/css2?family=Sansita:ital,wght@1,700&display=swap);


.underline {
  /* I don't know why this is needed, I just added it and it worked in IE Edge. If we remove this width, the gradient breaks in IE Edge */
  width: calc(100%);
  display: inline;
  background-image: linear-gradient(
    to bottom,
    #fff,
    #bbb 50%,
    #eee 75%,
    #ccc 75%
  );
  background-repeat: no-repeat;
  background-position: 0% 100%;
  background-size: 0% 25%;
  transition: background-size 0.5s;
}

*:hover > .underline.withhover {
  background-size: 100% 25%;
}

.underline.on {
  background-size: 100% 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

html,
body {
  background-color: black;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  background: transparent;
  cursor: default;
  background-color: #000000;
}

#root {
  width: 100%;
  height: 100%;
}

.form {
  background-color: white;
  padding: 32px;
  height: 75%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.form .email {
  background-color: white;
  padding: 8px;
  width: 216px;
  border-radius: 8px;
  font-size: 1em;
  margin: auto;
}

.form .email {
  -webkit-filter: drop-shadow(0px 0px 1px #333);
          filter: drop-shadow(0px 0px 1px #333);
}

.close {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
  cursor: pointer;
}
.close:hover {
  opacity: 1;
}
.close:before,
.close:after {
  position: absolute;
  left: 16px;
  content: " ";
  height: 32px;
  width: 2px;
  background-color: #333;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.willChange {
  will-change: transform, opacity;
}

.shadow {
  text-shadow: 5px 5px 0 #0d2a49;
}

